html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}:focus{outline:0}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration,input[type="search"]::-webkit-search-results-button,input[type="search"]::-webkit-search-results-decoration{-webkit-appearance:none;-moz-appearance:none}input[type="search"]{-webkit-appearance:none;-moz-appearance:none;box-sizing:content-box}textarea{overflow:auto;vertical-align:top;resize:vertical}audio,canvas,video{display:inline-block;*display:inline;*zoom:1;max-width:100%}audio:not([controls]){display:none;height:0}[hidden]{display:none}html{font-size:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%}a:focus{outline:thin dotted}a:active,a:hover{outline:0}img{border:0;-ms-interpolation-mode:bicubic}figure{margin:0}form{margin:0}fieldset{border:1px solid #c0c0c0;margin:0 2px;padding:0.35em 0.625em 0.75em}legend{border:0;padding:0;white-space:normal;*margin-left:-7px}button,input,select,textarea{font-size:100%;margin:0;vertical-align:baseline;*vertical-align:middle}button,input{line-height:normal}button,select{text-transform:none}button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:button;cursor:pointer;*overflow:visible}button[disabled],html input[disabled]{cursor:default}input[type="checkbox"],input[type="radio"]{box-sizing:border-box;padding:0;*height:13px;*width:13px}input[type="search"]{-webkit-appearance:textfield;box-sizing:content-box}input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none}button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0}textarea{overflow:auto;vertical-align:top}table{border-collapse:collapse;border-spacing:0}html,button,input,select,textarea{color:#222}::selection{background:#b3d4fc;text-shadow:none}img{vertical-align:middle}fieldset{border:0;margin:0;padding:0}textarea{resize:vertical}

html{font-size:62.5%}body{font-size:1.4rem;font-family:"Lato",sans-serif;color:#000}body .App{border:3rem solid #f5d6e7;min-height:calc(100vh - 6rem)}body .App header.header{position:relative}body .App header.header h1{margin:8rem auto 5rem;font-size:3.5rem;font-family:"Roboto",sans-serif;font-weight:900;letter-spacing:1.5rem;color:#000 !important;text-align:center}body .App header.header nav ul{width:50%;margin:auto;display:flex;justify-content:space-evenly}body .App header.header nav ul li{font-weight:800;width:calc(100% - 2rem);height:3rem;margin:auto 1rem;text-align:center}body .App header.header nav ul li a{width:100%;height:100%;padding-top:0.75rem;display:block;transition:all 0.2s}body .App header.header nav ul li a:hover{transition:all 0.1s;color:#e599c4}body .App header.header aside{position:absolute;top:-2rem;right:3rem}body .App header.header aside ul{display:flex;flex-direction:column}body .App header.header aside ul li{margin-bottom:2rem}body .App header.header aside ul li svg{fill:#000;width:3rem;height:3rem;transition:all 0.2s}body .App header.header aside ul li svg:hover{transition:all 0.1s;fill:#e599c4}body .App footer{margin:2rem}body .App footer ul{display:flex;justify-content:space-evenly;width:70%;margin:auto;font-size:1.3rem;font-weight:light}body .App .body-page{min-height:calc(100vh - 6rem - 25rem)}body a{text-decoration:none;color:#000}body a.active{color:#e599c4}

